/**
 * Rem-converter mixin to convert px to rem
 */
.list_block {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  gap: 10px;
}
.list_block div {
  width: 100%;
  transition: 0.2s;
  border-radius: 10px;
  padding: 40px 30px;
  background-color: #f6f6f6;
}
.list_block div p {
  margin-bottom: 20px;
  color: #005f9f;
  font-size: 20px;
  font-size: 1.25rem;
}
.list_block div ul li {
  line-height: 31px;
  list-style-type: " - ";
  list-style-position: inside;
}
.list_block div .last_items {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
.list_block div .last_items li {
  flex: 1 0 15%;
  display: block;
  text-align: center;
  align-content: center;
}
.list_block div .last_items li img {
  margin: 0 auto;
}

