.tabs_block {
  display: grid;
  grid-template-columns: 500px repeat(1, 1fr);
  grid-column-gap: 60px;
  margin-top: 50px;
}

.tabs {
  display: flex;
  flex-direction: column;
  padding: 50px 0;
  background-color: #f6f6f6;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
}

.tabs li:not(:last-child) {
  margin-right: 7px;
}

.tabs li a {
  display: block;
  position: relative;
  top: 4px;
  padding: 10px 34px;
  border-radius: 2px 2px 0 0;
  opacity: 0.7;
  transition: all 0.1s ease-in-out;
  color: #011e32;
  font-size: 24px;
  font-size: 1.5rem;
  line-height: 38px;
}

.tabs li.active a {
  font-weight: 500;
}
.tabs li.active a:before {
  content: "";
  border: 2px solid;
  position: relative;
  left: -34px;
  border-radius: 10px;
}

.tabs li.active a,
.tabs li a:hover {
  opacity: 1;
  color: #005f9f;
}

.tabs-content {
  position: relative;
  z-index: 2;
}

.tabs-panel {
  display: none;
}

.tabs-panel.active {
  display: block;
  animation: scale 0.7s ease-in-out;
}
.tabs-panel.active h3 {
  color: #011e32;
  font-size: 40px;
  font-size: 2.5rem;
  font-weight: 500;
}
.tabs-panel.active p {
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 38px;
  margin-top: 30px;
}

.tabs-panel p + div {
  margin-top: 15px;
}

@keyframes scale {
  0% {
    transform: scale(0.9);
    opacity: 0;
  }
  50% {
    transform: scale(1.005);
    opacity: 0.5;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
